import { Order_Food, Order_Add } from "./Types";

export const Orderfood = (data) => (dispatch) => {
  console.log(data);
  dispatch({
    type: Order_Food,
    payload: data,
  });
};

export const OrderAddress = (data) => (dispatch) => {
  console.log(data);
  dispatch({
    type: Order_Add,
    payload: data,
  });
};
export const getCart = (data) => (dispatch) => {
  console.log(req.params.id);
  defaultAxios.get(`/venders/${req.params.id}`).then((res) =>
    dispatch({
      type: Post_Add,
      payload: data,
    })
  );
};

export const Createcart = (data) => (dispatch) => {
  dispatch({
    type: "Add_cart",
    payload: data,
  });
};
